<script>
    import Countdown from './Countdown.vue'
    import 'v-toaster/dist/v-toaster.css'
    import telegram from './assets/telegram.png';
    import close_black from './assets/close-white.png';
    import btc_img from './assets/bitcoin.png';
    import btc_ln_img from './assets/lightning.png';
    import copy from './assets/copy.png';
    import hint_white from './assets/hint.png';
    import rocket from './assets/rocket.png';
    import sandwatch from './assets/sandwatch.png';

    export default {
        name: 'app',
        components: {
            'countdown': Countdown
        },
        props: {
            amount: {
                type: Number,
                required: true
            },
            currency: {
                type: String,
                default: 'uah'
            },
            order_id: {
                type: String,
                required: true
            },
            url: {
                type: String,
                default: '/index.php'
            },
            button: {
                type: String,
                default: 'button'
            },
        },
        data: function () {
            return {
                loading: false,
                show: false,
                amount: '',
                selected: '',
                step: 1,
                payment: {
                    qr_code: '',
                    qr: '',
                    show_qrcode: true,
                    peer_code: null,
                    peer: '',
                    show_qrpeer: false,
                },
                img: {
                    telegram: telegram,
                    close_black: close_black,
                    btc_img: btc_img,
                    btc_ln_img: btc_ln_img,
                    copy: copy,
                    hint_white: hint_white,
                    rocket: rocket,
                    sandwatch: sandwatch,
                },
                order_id: '',
                date: new Date().toUTCString().replace('GMT', ''),
                time: '',
                btc: '',
                error: 'Some errors',
                status: '',
                timer: {
                    start: '',
                    stop: ''
                },
            }
        },
        computed: {
            completeTime: function () {
                let d = new Date(this.timer.stop - this.timer.start);
                let minutes = d.getMinutes();
                let seconds = d.getSeconds();
                minutes = (minutes < 10) ? '0' + minutes : minutes;
                seconds = (seconds < 10) ? '0' + seconds : seconds;
                return minutes + ':' + seconds
            }
        },
        mounted() {
            let button = document.getElementById(this.button);
            button.addEventListener('click', () => {
                this.request();
            });

        },
        methods: {
            request: function () {
                this.loading = false;
                this.selected = '';
                this.axios.post(this.url, {
                    params: {
                        amount: this.amount,
                        currency: this.currency,
                    }
                }).then(response => {
                    this.btc = response.data.btc;
                    this.show = true;
                    this.step = 1;

                }).catch(error => {
                    this.error = error.response.data;
                    this.show = true;
                    this.step = 4;
                    if (error.response.status === 500) {
                        this.error = 'Widget off';
                    }
                })

            },
            requestPay: function () {
                this.loading = true;
                this.time = new Date().setMilliseconds(15 * 60 * 1000);
                this.timer.start = Date.now();
                    if (this.selected !== '') {
                        setTimeout(() => {
                            this.axios.post(this.url, {
                                params: {
                                    amount: this.amount,
                                    currency: this.currency,
                                    selected: this.selected,
                                    order_id: this.order_id,
                                }
                            }).then(response => {
                                this.payment.qr_code = response.data.payment_request;
                                this.payment.qr = 'data:image/png;base64,' + response.data.qr;
                                this.payment.peer_code = response.data.peer_code;
                                this.payment.peer = 'data:image/png;base64,' + response.data.peer;
                                this.loading = false;
                                this.step = 2;
                                this.checkStatus();
                            }).catch(error => {
                                this.error = error.response.data;
                                this.step = 4;
                                if (error.response.status === 500) {
                                    this.error = 'Widget off';
                                }
                                if (error.response.status === 503) {
                                    this.error = 'Service Unavailable';
                                }
                            })
                        }, 500);
                    } else {
                        this.$toaster.error('Please select a currency to pay');
                        this.loading = false;
                    }
            },
            checkStatus: function () {
                this.status = 'Сreating a new operation for payment.';
                this.loading = true;
                let status = setInterval(() => {
                    if (this.show === false) {
                        window.clearInterval(status);
                    } else {
                        this.axios.post(this.url, {
                            params: {
                                order_id: this.order_id,
                            }
                        }).then(response => {
                            if (response.data.status === 1) {
                                this.status = 'Waiting for payment confirmation.';
                                this.loading = true;
                            }
                            if (response.data.status === 2) {
                                window.clearInterval(status);
                                this.loading = false;
                                this.step = 3;
                                this.timer.stop = Date.now();
                                if (response.data.question) {
                                    this.question = response.data.question;
                                }

                            }
                            if (response.data.status === 3) {
                                window.clearInterval(status);
                                this.loading = false;
                                this.error = 'Operation canceled';
                                this.step = 4;
                            }

                        }).catch(error => {
                            this.error = error.response.data;
                            window.clearInterval(status);
                            this.loading = false;
                            this.step = 4;
                        })
                    }
                }, 10000);

            },
            copyCode: function () {
                let testingCodeToCopy = document.querySelector('#btcu-qr');
                testingCodeToCopy.setAttribute('type', 'text');
                testingCodeToCopy.select();
                document.execCommand('copy');
                this.$toaster.success('Code copied successfully!');

            },
            copyPeer: function () {
                let testingCodeToCopy = document.querySelector('#btcu-peer');
                testingCodeToCopy.setAttribute('type', 'text');
                testingCodeToCopy.select();
                document.execCommand('copy');
                this.$toaster.success('Peer copied successfully!');

            },
            proceed: function(){
                this.step = 31;
                this.timer.stop = Date.now()
            },
            timeout: function(){
                this.step = 32;
            },
            select: function (selected) {
                if (this.loading === false) {
                    this.selected = selected;
                }
            },
        }
    }
</script>

