      height: 16px;
      font-size: 13px;
      font-weight: 500;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.23;
      letter-spacing: normal;
      text-align: center;
      color: #70ad67;
      text-transform: none;
      padding: 0 182px;
    }

    .timeout_text {
      width: 530px;
      height: 36px;
      font-size: 12px;
      font-weight: 500;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.25;
      letter-spacing: normal;
      text-align: center;
      color: #ffffff;
      text-transform: none;
      padding: 15px 110px 0 90px;
    }

    .axxepted_text {

    }

    .slide-fade-enter-active {
        transition: all .3s ease;
    }

    .slide-fade-leave-active {
        transition: all .0s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    }

    .slide-fade-enter, .slide-fade-leave-to {
        transform: translateX(40px);
        opacity: 0;
    }

    .question-enter-active /* , .question-leave-active*/
    {
        transition: opacity .9s ease;
    }

    .question-enter, .question-leave-to {
        opacity: 0;
    }

    .question-leave-active {
        transition: opacity .0s;
    }


    .fade-enter-active, .fade-leave-active {
        transition: opacity .9s ease;
    }

    .fade-enter, .fade-leave-to {
        opacity: 0;
    }

    .btcu-feedback-active {
        color: red !important;
    }

    .fa-star {
        font-size: 40px;
        color: black;
        cursor: pointer;
    }

    .fa-thumbs-up, .fa-thumbs-down {
        font-size: 20px;
        text-align: center;
        margin: 10px 20px 10px 0;
        cursor: pointer;
    }

    .input_qr_code {
      width: 300px !important;
      height: 35px !important;
      border-radius: 4px;
      border: none;
      background-color: #151d26;
      padding: 2px 0;
      font-size: 12px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.25;
      letter-spacing: normal;
      text-align: center;
      color: #707070;
    }

    .btcu-success {
        color: #27ae60;
        font-size: 25px;
        text-align: center;
    }

    .fa-meh-o {
        color: orange !important;
    }

    .fa-smile-o, .fa-meh-o {
        color: #27ae60;
        text-align: center;
        font-size: 90px;
        display: block;
    }

    .btcu-error {
        color: red;
        font-size: 25px;
        text-align: center;
    }

    .fa-frown-o {
        color: red;
        text-align: center;
        font-size: 70px;
        display: block;
    }

    .btcu-modal-footer div {
        font-size: 10px;
        color: #888;
        margin: 10px 0 0px 0
    }

    .buy_credits_list li label .btcu-credits {
        background: #9e933c;
    }

    .buy_credits_list li label .btcu-amount {
        background: #f0dd34;
    }

    .buy_credits_list li label .btcu-choose {
        background: #89d57f;
    }

    .btcu-qr-code {
        width: 200px;
        height: 200px;
        object-fit: contain;
        padding: 28px 17px 0 72px;;
    }

    .btcu-payment span {
        padding-top: 3px;
        width: 60px;
        border: 1px solid #f4e640;
        height: 30px;
        text-align: center;
    }

    .btcu-payment-cart {
      margin: 13px 0 0 275px!important;
    }

    .btcu-payment {
        display: flex;
        flex: 1;
        align-items: center;
        height: 30px;
    }

    .fa-cus-form {
        font-size: 40px;
        margin-right: 10px;
        width: 30px;
        text-align: center;
    }

    .btcu-modal-body, .btcu-modal-header {
        text-transform: uppercase;
    }

    .btcu-currency-card {
        display: flex;
        flex: 1;
        float: left;
        cursor: pointer;
        padding: 20px 16px 20px 16px;
        border: 1px solid #f4e640;
        margin-bottom: 10px;
    }

    .btcu-currency {
        display: flex;
        flex: 1;
        justify-content: space-between;
        align-items: center;
      padding: 10px 24px 0 0;
    }

    .btcu-currency-card span {
        font-size: 18px;
        line-height: 1;
        display: inline-block;
        vertical-align: middle;
    }

    .btcu-title-cripto {
        float: right;
        padding-top: 10px;
    }

    .btcu-selected, .btcu-currency-card:hover {
        background-color: #9e9243;
        color: #f0dd34;
    }

    .btcu-modal-header {
        margin-bottom: 20px;
        height: 40px;
    }

    .btcu-modal-footer {
        margin-top: 20px;
        height: 30px;
    }

    .btcu-modal-body {
        height: auto;
    }

    label {
        cursor: pointer;
        transition: all 250ms;
    }

    .btcu-container {
        font-family: "Exo2Light", Arial, sans-serif;
        position: relative;
        margin: 0 auto;
        padding: 50px;
    }

    .btcu-container h2 {
        font-weight: 100;
        text-align: center;
    }

    .btcu-container .btcu-modal-button {
        position: relative;
        display: block;
        font-size: 36px;
        font-weight: 300;
        color: #2980b9;
        text-align: center;
        line-height: 0.3;
        border-radius: 3px;
        transition: all 250ms;
    }

    .btcu-container .btcu-modal-button span {
        font-size: 20px;
    }

    .btcu-container .btcu-modal-button:hover, .btcu-container .btcu-modal-button:focus {
        color: #16a085;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
    }

    .btcu-container .btcu-modal-content,
    .btcu-container .btcu-modal-background {
        visibility: hidden;
        width: 0;
        height: 0;
        opacity: 0;
        overflow: hidden;
        cursor: pointer;
    }

    .btcu-container .btcu-modal-background {
        transition: height 250ms;
    }

    .btcu-container .btcu-modal-content {
        transform: scale(0);
        transition: transform 500ms;
    }

    .btcu-container #btcu-modal {
        display: none;
    }

    .btcu-container .btcu-modal-background {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        visibility: visible;
        opacity: 0.8;
        transition: height 250ms;
    }

    .btcu-container .btcu-modal-content {
        position: fixed;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        color: #9e933c;
        visibility: visible;
        opacity: 1;
        pointer-events: auto;
        cursor: auto;
        transition: transform 500ms;
        width: 730px;
        height: 541px;
        border-radius: 16px;
        background-color: #17222c;
    }

    .btcu-container .btcu-modal-content .btcu-modal-close {
        position: absolute;
        top: 42.3px;
        right: 60.1px;
        font-size: 22px;
        color: rgba(51, 51, 51, 0.4);
        transition: color 200ms, transform 500ms;
    }

    .btcu-container .btcu-modal-content .btcu-modal-telegram {
      position: absolute;
      top: 42.3px;
      left: 60.1px;
      font-size: 22px;
      color: rgba(51, 51, 51, 0.4);
      transition: color 200ms, transform 500ms;
    }

    .btcu-modal-message {
        position: absolute;
        top: 10px;
        left: 20px;
        font-size: 22px;
        color: rgba(51, 51, 51, 0.4);
        margin-right: 20px
    }

    .btcu-container .btcu-modal-content .btcu-modal-close:hover, .btcu-container .btcu-modal-content .btcu-modal-close:focus {
        color: #333;
        transform: rotate(360deg);
    }

    .btcu-container .btcu-modal-content p {
        position: relative;
        margin: 0 0 20px 0;
        font-weight: 200;
        font-size: 20px;
        line-height: 1.6;
    }

    .btcu-container .btcu-modal-content .btcu-modal-content-button {
      width: 267.5px;
      height: 41px;
      border-radius: 4px;
      -webkit-backdrop-filter: blur(30px);
      backdrop-filter: blur(30px);
      box-shadow: 0 9px 10px 0 rgba(0, 0, 0, 0.16);
      line-height: 1.29;
      letter-spacing: normal;
      text-align: center;
      color: #ffffff;
      background-image: linear-gradient(91deg, #4da142, #dae489);
      font-size: 14px;
      font-weight: 600;
      font-style: normal;
      font-stretch: normal;
      border: none;
    }

    .btcu-modal-content-button{
      margin: 24px 244.5px 20px 214px;
    }

</style>



